.playerSelect {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex: 1 1;
  position: relative; }

.playerCard {
  text-align: center;
  background-color: #ddd;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 7px 10px;
  border-radius: 20px;
  box-shadow: 0px 10px 6px -6px #777; }
  .playerCard img {
    max-width: 100%;
    max-height: 100%; }
  .playerCard-info-container {
    max-width: 480px;
    max-height: 60px;
    display: flex;
    flex: 1 1;
    padding: 10px;
    position: relative; }
  .playerCard-color {
    width: 40px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px; }
  .playerCard-popover {
    position: absolute;
    z-index: 2;
    top: 0px;
    bottom: 0px;
    right: 0px; }
  .playerCard-cover {
    position: fixed;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px; }
  .playerCard-name {
    max-width: 480px;
    flex: 1 1; }
  .playerCard-picture {
    max-width: 480px;
    flex: 1 1; }
  .playerCard-remove {
    max-width: 480px;
    align-content: center;
    align-items: center;
    font-size: 1.8rem; }
    .playerCard-remove i {
      margin: 0.8rem; }
  .playerCard input[type="text"] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 0;
    background-color: transparent;
    outline: none;
    font-size: 1.1rem; }

